<template>
<v-snackbar v-model="$store.state.snackbar.show">
    {{ $store.state.snackbar.text }}

    <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="$store.dispatch('hideSnackbar')">
            Close
        </v-btn>
    </template>
</v-snackbar>
</template>

<script>
export default {
    name:'Snackbar'
}
</script>

